@import url("https://fonts.googleapis.com/css?family=Poppins:200i,400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Gluten:wght@500&display=swap");

@import "./variables";
@import "./mixins";

// COMPONENT IMPORTS
@import "../../Components/categories/categories";
@import "../../Components/header/header";
@import "../../Pages/login/login";
@import "../../Components/pagination/pagination";
@import "../../Components/recipes/recipes";
@import "../../Components/menu/menu";

@import "../../Pages/create/create";
@import "../../Pages/edit/edit";
@import "../../Pages/login/login";
@import "../../Pages/recipe/recipe";
@import "../../Pages/register/register";

// GLOBAL STYLING
html {
    scroll-behavior: smooth;
    }

body {
    font-family: Poppins, sans-serif;
    @include flex;
    background: rgb(255, 224, 254);
}

a {
    text-decoration: none;
    color: black;
}

ul {
    list-style: none;
}

button {
    border: none;
    background: none;
}

.form-back-button {
    position: absolute;
    top: 0;
    right: 0;
    font-size: 1.5rem;
}

.big-form-back-button {
    font-size: 1.5rem;
    position: absolute;
    right: 330px;
    top: 50px;
}

// APP STYLING
.global-container {
    width: clamp(300px, 90vw, 1300px);
    background: rgb(255, 255, 255);
    @include box;
    margin: 30px;
}

.main-container {
    background: rgb(255, 255, 255);
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(5, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;

    @include md {
        @include flex;
        flex-direction: column;
    }
}
